
@import url('https://fonts.googleapis.com/css?family=El+Messiri:600|Roboto:300|Spectral|Roboto|PT+Sans+Narrow&display=swap');
#app{
	font-family: 'Roboto', serif;
	font-weight: 500;
}
.authenticated {
	height: 100vh;
	width: calc(100vw-15px);
	display: grid;
	grid-template-rows: -webkit-max-content auto -webkit-max-content;
	grid-template-rows: max-content auto max-content;
}
/**/
.load{
	height: 100vh;
	width: 100vw;
	display: grid;
	grid-template-rows: auto -webkit-max-content auto;
	grid-template-rows: auto max-content auto;
}
.loadText{
	margin: 0 auto;
	background: #64b2db;
	padding: 10px;
}
.load p{
	color: white;
	padding: 0px;
	text-align: center;
}
/**/
.loadSvg{
    fill: white;
    enable-background: new 0 0 16 16;
    padding: 15px;
    margin-left: 15px;
    height: 30px;
    width: 30px;
    -webkit-animation: spin 3s linear infinite;
            animation: spin 3s linear infinite;
}
@-webkit-keyframes spin {
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
@keyframes spin {
100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
}
}
/**/
*:not(svg):hover{
	-webkit-transition: all 0.2s;
	transition: all 0.2s;
}
router-view{
	overflow-y: auto;
	height: 100vh;
}
@media (max-width: 500px) {
*{
		font-size: 14px;
}
}
